* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  color: #293264;
}

body {
  background-color: #f5f7fb;
}

.karla {
  font-family: "Karla", sans-serif;
}

.inter {
  font-family: "Inter", sans-serif;
}

button {
  cursor: pointer;
}

main {
  padding: 1rem 2rem;
  position: relative;
  width: 100vw;
  min-height: 100vh;
}

main::before,
main::after {
  position: fixed;
}

main::before {
  content: url(../images/yellow-blob.svg);
  right: 0;
  top: 0;
  z-index: -1;
}

main::after {
  content: url(../images/blue-blob.svg);
  bottom: 0;
  left: 0;
  z-index: -1;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.loading span {
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 700;
  animation-name: flash;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-direction: alternate;
}

.clicked {
  background-color: #d6dbf5;
  border-color: transparent !important;
}

.start-page {
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.start-page h1 {
  font-size: 2.2rem;
}
.start-page h2 {
  margin-top: 20px;
  font-weight: 400;
  font-size: 1.2rem;
  margin-bottom: 30px;
}
.start-page input[type=number] {
  width: fit-content;
  display: inline;
  margin: 0;
  text-align: center;
}
.start-page input[type=number]:invalid:required {
  background-color: #f8bcbc;
}
.start-page .input-constraint-info {
  opacity: 0.6;
  font-size: 1rem;
  font-weight: 700;
  display: block;
}
.start-page button {
  margin-top: 20px;
}

div[class*=question] {
  width: 100%;
}

div[class*=question] > h3 {
  margin-bottom: 12px;
}

div[class*=question] > hr {
  margin: 15px 0;
  border: none;
  background-color: #dbdef0;
  height: 2px;
}

div[class*=answers] {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.6rem;
  width: 100%;
}

div[class*=answers] > div {
  padding: 4px 20px;
  border: 2px solid #293264;
  border-radius: 8px;
  cursor: pointer;
}

.result {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  gap: 30px;
  justify-content: center;
}

.result > button {
  color: #f5f7fb;
  border-radius: 10px;
  padding: 8px 14px;
  border: none;
  background-color: #4d5b9e;
}

.check-answer-btn {
  display: block;
  padding: 10px 20px;
  background-color: #4d5b9e;
  color: #f5f7fb;
  border: none;
  border-radius: 10px;
  margin: auto;
}

@media (min-width: 768px) {
  .main {
    padding: 40px 75px;
  }

  .loading span {
    font-size: 3rem;
  }

  .start-page h1 {
    font-size: 3rem;
  }
  .start-page h2 {
    font-size: 1.5rem;
  }
  .start-page form > *,
.start-page .input-constraint-info {
    font-size: 1.2rem;
  }
}
@keyframes flash {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

